@import '../../../../assets/styles/_colors.scss';

footer.tab {
    display: flex;
    flex-direction: column;
    position: relative;
    bottom:0;
    width: 100%;
    min-height: 140px;
    height: 140px;
    background-color: #fff;
    border-left: 1px solid $border-color;
    border-top: 1px solid $border-color;
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    overflow: hidden;
    z-index:2;
}
footer.tab .m {
    padding-top: 50px;
    height:100%;
}
footer.tab * {
    user-select: none;
}
footer.tab.up {
    height: auto;
    height: 50%;
    overflow-y: auto;
}
footer.tab.up.half {
    height: 50% !important;
}
.prevnext {
    position: absolute;
    top:0;
    width: 100%;
    height: 50px;
    padding: 9px 20px;
    border-bottom: 1px solid $grey;
    text-align: center;
}
.prevnext p {
    display: inline-block;
    line-height: 32px;
    font-size: 16px;
    color:#606060;
    text-transform: uppercase;
    padding: 0 12px;
}
.prevnext p.pn {
    cursor:pointer;
    border-radius: 2px;
}
.prevnext p.prev {
    float:left;
}
.prevnext p.next {
    float: right;
}
.prevnext p.pn:hover {
    background-color:#f2f2f2;
}
.prevnext p.pn:active {
    background-color: #e5e5e5;
}
.tab .vehicle-container {
    background-color: #f5f5f5;
}
.tab .vehicle-container .vehicle p.name {
    font-size: 16px;
}
.tab .vehicle-container .vehicle p.date {
    font-size: 14px;
}
.tab .buttons {
    border-top: 1px solid #e5e5e5;
    padding: 20px 0 0 0;
}
.tab button.button {
    margin-top: 0;
}

.settings {
    display: none;
}
.settings.show {
    display: block
}

@media screen and (max-width:500px) {
    footer.tab.up {
        height: 100%;
    }
    .tab .buttons {
        padding: 20px 0 10px 0;
    }
}